/* Validated Input */
.validated-input-container {
  margin-bottom: 1rem;
}

.input-label-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.25rem;
}

.input-label {
  font-weight: 500;
  color: var(--text-primary);
  font-size: 0.9rem;
}

.required-indicator {
  color: var(--error-color, #dc3545);
  font-weight: bold;
}

.help-container {
  position: relative;
}

.help-button {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: var(--info-color, #17a2b8);
  color: white;
  border: none;
  font-size: 0.7rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.help-button:hover {
  background-color: var(--info-hover, #138496);
}

.help-tooltip {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--tooltip-bg, #333);
  color: var(--tooltip-text, #fff);
  padding: 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  white-space: nowrap;
  z-index: 1000;
  margin-top: 0.25rem;
  max-width: 250px;
  white-space: normal;
}

.help-tooltip::before {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 4px solid transparent;
  border-bottom-color: var(--tooltip-bg, #333);
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.validated-input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid var(--border-color, #ced4da);
  border-radius: 4px;
  font-size: 0.9rem;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.validated-input:focus {
  outline: none;
  border-color: var(--primary-color, #007bff);
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

.validated-input.has-errors {
  border-color: var(--error-color, #dc3545);
  padding-right: 2.5rem;
}

.validated-input.has-errors:focus {
  border-color: var(--error-color, #dc3545);
  box-shadow: 0 0 0 2px rgba(220, 53, 69, 0.25);
}

.validated-input.has-warnings {
  border-color: var(--warning-color, #ffc107);
  padding-right: 2.5rem;
}

.validated-input.has-warnings:focus {
  border-color: var(--warning-color, #ffc107);
  box-shadow: 0 0 0 2px rgba(255, 193, 7, 0.25);
}

.validation-indicator {
  position: absolute;
  right: 0.5rem;
  font-size: 0.8rem;
  pointer-events: none;
}

/* Validated Select */
.validated-select-container {
  margin-bottom: 1rem;
}

.select-label-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.25rem;
}

.select-label {
  font-weight: 500;
  color: var(--text-primary);
  font-size: 0.9rem;
}

.select-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.validated-select {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid var(--border-color, #ced4da);
  border-radius: 4px;
  font-size: 0.9rem;
  background-color: var(--bg-color, #fff);
  transition: border-color 0.2s, box-shadow 0.2s;
}

.validated-select:focus {
  outline: none;
  border-color: var(--primary-color, #007bff);
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

.validated-select.has-errors {
  border-color: var(--error-color, #dc3545);
  padding-right: 2.5rem;
}

.validated-select.has-errors:focus {
  border-color: var(--error-color, #dc3545);
  box-shadow: 0 0 0 2px rgba(220, 53, 69, 0.25);
}

.validated-select.has-warnings {
  border-color: var(--warning-color, #ffc107);
  padding-right: 2.5rem;
}

.validated-select.has-warnings:focus {
  border-color: var(--warning-color, #ffc107);
  box-shadow: 0 0 0 2px rgba(255, 193, 7, 0.25);
}

/* Responsive Design */
@media (max-width: 768px) {
  .help-tooltip {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 90vw;
  }
  
  .help-tooltip::before {
