  .clear-button {
    transition: none;
  }
}
.key-recorder-help {
  margin-top: 0.5rem;
  padding: 0.5rem;
  background: #f0f8ff;
  border: 1px solid #cce7ff;
  border-radius: 4px;
  font-size: 0.85rem;
}

.key-recorder-help strong {
  color: #2c3e50;
}

.recording-status small br {
  margin: 0.25rem 0;
}.common-keys
 {
  margin-top: 0.5rem;
  padding-top: 0.5rem;
  border-top: 1px solid #ddd;
}

.common-keys strong {
  display: block;
  margin-bottom: 0.5rem;
  color: #2c3e50;
  font-size: 0.8rem;
}

.key-suggestions {
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
}

.key-suggestions button {
  padding: 0.25rem 0.5rem;
  border: 1px solid #ddd;
  border-radius: 3px;
  background: white;
  cursor: pointer;
  font-size: 0.75rem;
  transition: all 0.2s ease;
}

.key-suggestions button:hover {
  background: #f0f0f0;
  border-color: #3498db;
  color: #3498db;
}

/* Responsive design for key suggestions */
@media (max-width: 768px) {
  .key-suggestions {
    flex-direction: column;
  }
  
  .key-suggestions button {
    text-align: center;
  }
}.su
per-key-section {
  margin-top: 0.75rem;
  padding-top: 0.5rem;
  border-top: 1px solid #e0e0e0;
}

.super-key-section strong {
  display: block;
  margin-bottom: 0.5rem;
  color: #e67e22;
  font-size: 0.8rem;
}

.super-key-section .key-suggestions button {
  background: #fff3cd;
  border-color: #ffeaa7;
  color: #856404;
}

.super-key-section .key-suggestions button:hover {
  background: #ffeaa7;
  border-color: #e67e22;
  color: #d35400;
}.m
anual-typing-guide {
  margin-top: 0.75rem;
  padding-top: 0.5rem;
  border-top: 1px solid #e0e0e0;
}

.manual-typing-guide summary {
  cursor: pointer;
  font-size: 0.8rem;
  color: #666;
  margin-bottom: 0.5rem;
}

.manual-typing-guide summary:hover {
  color: #3498db;
}

.key-reference {
  margin-top: 0.5rem;
  font-size: 0.75rem;
}

.key-category {
  margin-bottom: 0.5rem;
}

.key-category strong {
  display: block;
  margin-bottom: 0.25rem;
  color: #2c3e50;
}

.key-category code {
  background: #f8f9fa;
  padding: 0.25rem;
  border-radius: 3px;
  font-family: monospace;
  font-size: 0.7rem;
  display: block;
  word-wrap: break-word;
}.debug-
button {
  padding: 0.5rem 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: white;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.debug-button:hover {
  background: #f0f8ff;
  border-color: #3498db;
  color: #3498db;
}

.debug-panel {
  margin-top: 0.5rem;
  padding: 0.75rem;
  background: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  font-size: 0.85rem;
}

.debug-panel strong {
  color: var(--text-primary);
  display: block;
  margin-bottom: 0.5rem;
}

.debug-panel p {
  margin: 0.5rem 0;
  color: var(--text-secondary);
}

.last-key-event {
  margin: 0.5rem 0;
  padding: 0.5rem;
  background: #e9ecef;
  border-radius: 3px;
}

.last-key-event code {
  background: #fff;
  padding: 0.25rem;
  border-radius: 2px;
  font-family: monospace;
}

.debug-panel small {
  color: var(--text-secondary);
  font-style: italic;
}

/* SmartShift and Axis Actions Configuration Styles */
.smartshift-preview,
.axis-info {
  margin-top: 1rem;
  padding: 1rem;
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 4px;
}

.smartshift-preview h5,
.axis-info h5 {
  margin: 0 0 0.5rem 0;
  color: var(--text-primary);
  font-size: 0.9rem;
}

.config-preview {
  background: #2d3748;
  color: #e2e8f0;
  padding: 1rem;
  border-radius: 4px;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 0.8rem;
  line-height: 1.4;
  overflow-x: auto;
}

.axis-actions-section {
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid #e9ecef;
}

.axis-actions-section h4 {
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.axis-actions-section p {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.axis-setup {
  text-align: center;
  padding: 2rem;
  background: #f8f9fa;
  border: 2px dashed #dee2e6;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.axis-setup p {
  color: var(--text-secondary);
  margin-bottom: 1rem;
}

.setup-axis-button {
  background: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.75rem 1.5rem;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.setup-axis-button:hover {
  background: #218838;
}

.axis-config {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  padding: 1.5rem;
}

.axis-config .form-group {
  margin-bottom: 1rem;
}

.axis-config .form-group:last-child {
  margin-bottom: 0;
}

.axis-config .form-group label {
  display: block;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.axis-config .form-group input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ced4da;
  border-radius: 4px;
  font-size: 0.9rem;
}

.axis-config .form-group input:focus {
  outline: none;
  border-color: #80bdff;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.axis-config .form-group small {
  display: block;
  margin-top: 0.25rem;
  color: #6c757d;
  font-size: 0.8rem;
}

.remove-axis-button {
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
  cursor: pointer;
  margin-top: 1rem;
  transition: background-color 0.2s ease;
}

.remove-axis-button:hover {
  background: #c82333;
}

/* Responsive design for new sections */
@media (max-width: 768px) {
  .axis-setup {
    padding: 1.5rem;
  }
  
  .axis-config {
    padding: 1rem;
  }
  
  .config-preview {
    font-size: 0.75rem;
    padding: 0.75rem;
  }
  
  .smartshift-preview,
  .axis-info {
    padding: 0.75rem;
  }
}

@media (max-width: 480px) {
  .axis-setup {
    padding: 1rem;
  }
  
  .setup-axis-button {
    padding: 0.6rem 1.2rem;
    font-size: 0.85rem;
  }
  
  .axis-config {
    padding: 0.75rem;
  }
  
  .config-preview {
    font-size: 0.7rem;
    padding: 0.5rem;
  }
  
  .remove-axis-button {
    padding: 0.4rem 0.8rem;
    font-size: 0.8rem;
  }
}

/* Dark theme support for new sections */
@media (prefers-color-scheme: dark) {
  .axis-setup {
    background: #2d3748;
    border-color: #4a5568;
  }
  
  .axis-config,
  .smartshift-preview,
  .axis-info {
    background: #2d3748;
    border-color: #4a5568;
  }
  
  .config-preview {
    background: #1a202c;
    color: #e2e8f0;
  }
  
  .axis-config .form-group input {
    background: #2d3748;
    border-color: #4a5568;
    color: #e2e8f0;
  }
  
  .axis-config .form-group input:focus {
    border-color: #63b3ed;
    box-shadow: 0 0 0 0.2rem rgba(99, 179, 237, 0.25);
  }
}

/* Preferences Dialog Styles */
.preferences-dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(2px);
  animation: fadeIn var(--transition-normal);
}

.preferences-dialog {
  background: var(--bg-primary);
  border-radius: 8px;
  box-shadow: var(--shadow-heavy);
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow: hidden;
  animation: scaleIn var(--transition-normal) var(--animation-bounce);
}

.preferences-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-secondary);
}

.preferences-header h2 {
  margin: 0;
  color: var(--text-primary);
  font-size: 1.25rem;
}

.close-button {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: var(--text-secondary);
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 4px;
  transition: all var(--transition-fast);
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-button:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.preferences-content {
  padding: 1.5rem;
  max-height: 60vh;
  overflow-y: auto;
}

.preference-section {
  margin-bottom: 2rem;
}

.preference-section:last-child {
  margin-bottom: 0;
}

.preference-section h3 {
  margin: 0 0 1rem 0;
  color: var(--text-primary);
  font-size: 1.1rem;
  font-weight: 600;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 0.5rem;
}

.preference-item {
  margin-bottom: 1rem;
}

.preference-item:last-child {
  margin-bottom: 0;
}

.preference-item label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-primary);
  font-size: 0.9rem;
  font-weight: 500;
}

.preference-item input[type="checkbox"] {
  margin: 0;
  accent-color: var(--accent-primary);
}

.preference-item input[type="number"],
.preference-item select {
  padding: 0.5rem;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background: var(--bg-primary);
  color: var(--text-primary);
  font-size: 0.9rem;
  transition: border-color var(--transition-fast);
}

.preference-item input[type="number"]:focus,
.preference-item select:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
}

.preference-item input[type="number"]:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Theme Selector */
.theme-selector {
  margin-top: 0.5rem;
}

.theme-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1rem;
}

.theme-option {
  display: flex !important;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  padding: 1rem;
  border: 2px solid var(--border-color);
  border-radius: 8px;
  transition: all var(--transition-fast);
  background: var(--bg-secondary);
}

.theme-option:hover {
  border-color: var(--accent-primary);
  transform: translateY(-2px);
  box-shadow: var(--shadow-light);
}

.theme-option input[type="radio"] {
  margin: 0;
  accent-color: var(--accent-primary);
}

.theme-option input[type="radio"]:checked + .theme-preview {
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
}

.theme-preview {
  width: 60px;
  height: 40px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  overflow: hidden;
  transition: all var(--transition-fast);
}

.theme-preview-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.theme-preview-header {
  height: 30%;
  background: #f8f9fa;
}

.theme-preview-body {
  flex: 1;
  background: #ffffff;
}

.theme-preview.dark .theme-preview-header {
  background: #2d2d30;
}

.theme-preview.dark .theme-preview-body {
  background: #1e1e1e;
}

.theme-preview.system .theme-preview-header {
  background: linear-gradient(90deg, #f8f9fa 50%, #2d2d30 50%);
}

.theme-preview.system .theme-preview-body {
  background: linear-gradient(90deg, #ffffff 50%, #1e1e1e 50%);
}

.theme-option span {
  font-size: 0.85rem;
  color: var(--text-primary);
  font-weight: 500;
}

.preferences-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem 1.5rem;
  border-top: 1px solid var(--border-color);
  background: var(--bg-secondary);
}

.button-primary,
.button-secondary {
  padding: 0.5rem 1.5rem;
  border: none;
  border-radius: 4px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-fast);
  position: relative;
  overflow: hidden;
}

.button-primary {
  background: var(--accent-primary);
  color: white;
}

.button-primary:hover {
  background: var(--accent-hover);
  transform: translateY(-1px);
  box-shadow: var(--shadow-light);
}

.button-secondary {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.button-secondary:hover {
  background: var(--bg-secondary);
  border-color: var(--border-hover);
  transform: translateY(-1px);
  box-shadow: var(--shadow-light);
}

/* Enhanced Component Animations */
.config-section {
  animation: slideInUp var(--transition-normal);
}

.config-section:nth-child(even) {
  animation-delay: 0.1s;
}

.config-section:nth-child(odd) {
  animation-delay: 0.05s;
}

.welcome-message {
  animation: fadeIn var(--transition-slow);
}

.toolbar-button {
  position: relative;
}

.toolbar-button.active {
  animation: bounceIn 0.4s var(--animation-bounce);
}

/* Performance Indicators */
.performance-indicator {
  position: fixed;
  top: 10px;
  right: 10px;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  padding: 0.5rem;
  font-size: 0.8rem;
  color: var(--text-secondary);
  z-index: 1000;
  opacity: 0.8;
  transition: opacity var(--transition-fast);
}

.performance-indicator:hover {
  opacity: 1;
}

.performance-warning {
  color: var(--warning-color);
  font-weight: 600;
}

/* Responsive Design for Preferences */
@media (max-width: 768px) {
  .preferences-dialog {
    width: 95%;
    margin: 1rem;
  }
  
  .preferences-header,
  .preferences-footer {
    padding: 1rem;
  }
  
  .preferences-content {
    padding: 1rem;
  }
  
  .theme-options {
    grid-template-columns: 1fr;
  }
  
  .theme-option {
    flex-direction: row !important;
    justify-content: flex-start;
    padding: 0.75rem;
  }
  
  .theme-preview {
    width: 40px;
    height: 30px;
  }
}

@media (max-width: 480px) {
  .preferences-dialog {
    width: 100%;
    height: 100%;
    max-height: 100vh;
    border-radius: 0;
  }
  
  .preferences-header h2 {
    font-size: 1.1rem;
  }
  
  .preference-section h3 {
    font-size: 1rem;
  }
  
  .preferences-footer {
    flex-direction: column;
  }
  
  .button-primary,
  .button-secondary {
    width: 100%;
    padding: 0.75rem;
  }
}

/* Dark theme specific enhancements */
[data-theme="dark"] .preferences-dialog {
  border: 1px solid var(--border-color);
}

[data-theme="dark"] .theme-preview {
  border-color: var(--border-color);
}

/* High contrast mode for preferences */
@media (prefers-contrast: high) {
  .theme-option {
    border-width: 3px;
  }
  
  .theme-option:hover {
    border-color: var(--text-primary);
  }
  
  .button-primary,
  .button-secondary {
    border: 2px solid var(--text-primary);
  }
}

/* Reduced motion for preferences */
@media (prefers-reduced-motion: reduce) {
  .preferences-dialog {
    animation: none;
  }
  
  .theme-option:hover {
    transform: none;
  }
  
  .button-primary:hover,
  .button-secondary:hover {
    transform: none;
